<template>
  <div class="d-flex" style="gap: 15px">
    <div class="d-flex">
      <BranchDropdown @onBranchSelection="(e) => handleBranchSelect(e, 'base')" :branches="items" title="base:"></BranchDropdown>
      <v-icon class="pl-3 pb-7">mdi-arrow-left</v-icon>
      <BranchDropdown @onBranchSelection="(e) => handleBranchSelect(e, 'compare')" :branches="items" title="compare:"></BranchDropdown>
    </div>
    <div class="d-flex flex">
      <input class="flex repo-list-search" type="text" placeholder="Pull Request Title" autocomplete="off" aria-label="title" v-model="prTitle" value="" />
      <v-btn :disabled="!isDifferent || prTitle.length === 0" x-large class="ml-4 button" @click="createPR()">Create Pull Request</v-btn>
    </div>
  </div>
</template>

<script>
import BranchDropdown from "./BranchDropdown";
import { mapActions } from "vuex";
export default {
  name: "BranchDiffBar",
  props: {
    branches: {
      type: Array,
      default: function () {
        return null;
      },
    },
  },
  data: () => ({
    compareBranch: "",
    baseBranch: "",
    isDifferent: false,
    prTitle: "",
  }),
  methods: {
    ...mapActions(["createPullRequest"]),
    compareBranches(branchOne, branchTwo) {
      return branchOne === null ? true : branchTwo !== "" ? branchOne === branchTwo : true;
    },
    handleBranchSelect(e, side) {
      if (side === "base") this.baseBranch = e;
      else this.compareBranch = e;
      this.$emit("updateBranches", this.baseBranch, this.compareBranch);
    },
    async createPR() {
      try {
        const pullNumber = await this.createPullRequest({
          repoUrl: this.$route.params.repoUrl,
          title: this.prTitle,
          head: this.compareBranch,
          base: this.baseBranch,
        });
        history.replaceState(null, "", `/${this.$route.params.repoUrl}/pulls/${pullNumber}`);
        await this.$router.push({ path: `/${this.$route.params.repoUrl}/pulls/${pullNumber}/bug-injection` });
      } catch (e) {
        this.$emit("errorHandler", e.message);
      }
    },
  },
  watch: {
    compareBranch: function (val) {
      this.isDifferent = !this.compareBranches(val, this.baseBranch);
      this.$emit("showCommits", this.isDifferent);
    },
    baseBranch: function (val) {
      this.isDifferent = !this.compareBranches(val, this.compareBranch);
      this.$emit("showCommits", this.isDifferent);
    },
  },
  computed: {
    items() {
      return this.branches.map((branch) => branch.name);
    },
  },
  components: {
    BranchDropdown,
  },
};
</script>

<style scoped>
.button {
  background: #238636 !important;
  border: 0.5px solid #cccccc !important;
  border-radius: 10px !important;
  font-style: normal;
  font-weight: bold;
  font-size: 17px !important;
  line-height: 25px;
  display: flex;
  align-items: center;
  color: #ffffff;
}
</style>
