<template>
  <div>
    <Loader :visible="!dataReady" />
    <MainLayout pageTitle="Comparing changes">
      <div slot="prepend">
        <div class="ml-4 mb-4 info-text">Choose two branches to see whats changed or to start a new pull request.</div>
        <BranchDiffBar :branches="currentBranches" @errorHandler="setPrError" @showCommits="showCommits" @updateBranches="update"></BranchDiffBar>
        <div v-visible="!!prErrorMsg" class="ml-4 mt-n4 red--text">{{ prErrorMsg ? prErrorMsg : "Error Filler" }}</div>
      </div>
      <div v-if="show && dataReady">
        <CommitList :commits="this.commits" :base-branch="baseBranch" :compare-branch="compareBranch" :total="totalCommits" />
        <v-pagination @input="changePage" v-model="pageNo" dark :length="length" ref="pagination" v-if="commits.length" />
      </div>
    </MainLayout>
  </div>
</template>

<script>
import MainLayout from "../components/MainLayout.vue";
import BranchDiffBar from "../components/BranchDiffBar";
import { mapActions, mapGetters } from "vuex";
import CommitList from "../components/CommitList";
import Loader from "../components/Loader.vue";
export default {
  name: "BranchDifference",
  components: { CommitList, MainLayout, BranchDiffBar, Loader },
  data: () => ({
    prErrorMsg: undefined,
    show: false,
    baseBranch: "",
    compareBranch: "",
    commits: [],
    pageNo: 1,
    itemsPerPage: 10,
    totalCommits: 0,
    dataReady: true,
  }),
  methods: {
    ...mapActions(["getRepoBranches", "compareTwoCommits", "createPullRequest"]),
    setPrError(prErrorMsg) {
      this.prErrorMsg = prErrorMsg;
    },
    changePage(pageNo) {
      this.showCommits(true, pageNo);
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },
    async showCommits(show, pageNo) {
      this.show = show;
      if (show) {
        this.dataReady = false;
        if (!this.currentBranches.some((branch) => branch.name === this.baseBranch) || !this.currentBranches.some((branch) => branch.name === this.compareBranch)) {
          this.show = false;
          this.dataReady = true;
          return;
        }
        const result = await this.compareTwoCommits({
          repoUrl: this.$route.params.repoUrl,
          base: this.baseBranch,
          compare: this.compareBranch,
          pageNo: pageNo,
          pageSize: this.itemsPerPage,
        });

        this.totalCommits = result.total_commits;
        this.commits = [];

        for (const commit of result.commits) {
          this.commits.push({
            author: commit.author ? commit.author.login : commit.commit.author.name,
            title: commit.commit.message.split("\n")[0],
            date: commit.commit.author.date,
            sha: commit.sha,
            avatarUrl: commit.author ? commit.author.avatar_url : "noicon",
          });
        }
        this.dataReady = true;
      }
    },
    update(base, compare) {
      this.baseBranch = base;
      this.compareBranch = compare;
      this.pageNo = 1;
    },
  },
  computed: {
    ...mapGetters({
      currentBranches: "branches",
    }),
    length() {
      return Math.ceil(this.totalCommits / this.itemsPerPage);
    },
  },
  mounted() {
    this.getRepoBranches(this.$route.params.repoUrl);
  },
};
</script>

<style scoped>
.info-text {
  font-size: 14px;
  color: #8b949e;
  flex: 1 100%;
  text-align: left;
}
</style>
