<template>
  <v-combobox dark filled outlined solo class="ml-4" @input="setBranch" :label="title" :items="branches"></v-combobox>
</template>
<script>
export default {
  name: "BranchDropdown",
  props: {
    title: {
      type: String,
    },
    branches: {
      type: Array,
      default: function () {
        return null;
      },
    },
  },
  data: () => ({
    selectedBranch: "",
  }),
  methods: {
    setBranch(value) {
      this.selectedBranch = value;
      this.$emit("onBranchSelection", value);
    },
  },
};
</script>
