














import moment from "moment";
import UserIcon from "@/components/UserIcon.vue";
export default {
  name: "CommitItem",
  components: { UserIcon },
  props: {
    commit: Object,
  },
  methods: {
    format(e: string) {
      return moment(e).format("MMMM Do, YYYY - hh:mma");
    },
    abbreviateOid(oid: string) {
      return oid.substr(0, 7);
    },
  },
};
